/*===== global styles =====*/
* {
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Noto Sans", sans-serif;
  color: #35383f;
}

body.lock {
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1532px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 991px) {
  .container {
    padding: 0 20px;
  }
}

a {
  color: inherit;
  text-decoration: none;
  outline: none;
}

html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul,
li {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}

ol,
ul {
  list-style: none;
}

button,
input {
  padding: 0;
  border: none;
  appearance: none;
  outline: none;
  background-color: transparent;
}

button,
a {
  cursor: pointer;
}

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 3;
  width: 100%;
}

.header__container {
  min-height: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0 40px;
}
@media (max-width: 767px) {
  .header__container {
    min-height: 88px;
    padding: 0 30px;
  }
}

.header__burger_btn {
  flex-shrink: 0;
  z-index: 3;
  width: 28px;
  height: 28px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}
@media (max-width: 1300px) {
  .header__burger_btn {
    opacity: 1;
    visibility: visible;
    pointer-events: all;
  }
}
.header__burger_btn.active .header__icon span {
  opacity: 0;
}
.header__burger_btn.active .header__icon::after, .header__burger_btn.active .header__icon::before {
  transform: translateY(0);
}
.header__burger_btn.active .header__icon::after {
  transform: rotate(45deg);
}
.header__burger_btn.active .header__icon::before {
  transform: rotate(-45deg);
}
@media (hover: hover) {
  .header__burger_btn:hover .header__icon span, .header__burger_btn:hover .header__icon::before, .header__burger_btn:hover .header__icon::after {
    background-color: #6682a6;
  }
}

.header__icon {
  position: relative;
  display: inline-block;
  width: 21px;
  height: 14px;
}
.header__icon span {
  position: absolute;
  left: 0;
  top: 6px;
  display: inline-block;
  width: 100%;
  height: 2px;
  background-color: #002f6c;
  border-radius: 2px;
  transition: opacity 0.2s ease-out, color 0.2s ease-out;
}
.header__icon::after, .header__icon::before {
  content: "";
  position: absolute;
  left: 0;
  top: 6px;
  display: inline-block;
  width: 100%;
  height: 2px;
  background-color: #002f6c;
  border-radius: 2px;
  transition: transform 0.2s ease-out, background-color 0.2s ease-out;
}
.header__icon::before {
  transform: translateY(-6px);
}
.header__icon::after {
  transform: translateY(6px);
}

.header__nav {
  height: 100%;
  transform: translateX(30px);
}
@media (max-width: 1300px) {
  .header__nav {
    position: fixed;
    top: 0;
    left: 100%;
    width: 100%;
    z-index: 2;
    padding-top: 90px;
    background-color: #fff;
    transition: transform 0.3s ease-out;
    background-color: #fff;
    transform: translateX(0);
  }
  .header__nav::before {
    content: "";
    position: absolute;
    top: 90px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #f2f2f2;
  }
  .header__nav.active {
    transform: translateX(-100%);
  }
}

.header__list {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0 31px;
}
.header__list a {
  font-weight: 500;
  font-size: 18px;
  line-height: 23px;
  color: #002f6c;
  transition: color 0.2s ease-out;
}
@media (hover: hover) {
  .header__list a:hover {
    color: #6682a6;
  }
}
@media (max-width: 1300px) {
  .header__list {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 0;
    overflow-y: auto;
  }
  .header__list a {
    display: inline-block;
    padding: 32px 0;
  }
}

.header__lang {
  position: relative;
  z-index: 3;
  min-width: 150px;
  height: 100%;
  display: flex;
  align-items: center;
  gap: 0 4px;
  font-weight: 500;
  font-size: 18px;
  color: #002f6c;
  cursor: pointer;
}
.header__lang span {
  position: relative;
  transition: color 0.2s ease-out;
}
.header__lang span::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #002f6c;
  transition: background-color 0.2s ease-out;
}
.header__lang svg path {
  transition: all 0.2s ease-out;
}
@media (hover: hover) {
  .header__lang:hover span {
    color: #6682a6;
  }
  .header__lang:hover span::after {
    background-color: #6682a6;
  }
  .header__lang:hover svg path {
    fill: #6682a6;
  }
}

.header__lang_wrapper {
  position: relative;
  z-index: 4;
}

.header__switch_lang {
  position: absolute;
  top: calc(100% + 16px);
  right: 0;
  min-width: 195px;
  min-height: 100px;
  z-index: 2;
  padding: 19px 22px 19px 18px;
  border-radius: 8px;
  box-shadow: 4px 4px 12px 0 rgba(0, 0, 0, 0.1);
  background: #fff;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 0.2s ease-out, visibility 0.2s ease-out;
}
.header__switch_lang.active {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}
.header__switch_lang svg {
  position: absolute;
  top: 10px;
  right: 12px;
  width: 8px;
  height: auto;
}

.header__label {
  cursor: pointer;
}
.header__label:not(:last-child) {
  margin-bottom: 12px;
}
.header__label input {
  box-sizing: border-box;
  padding: 0;
  border: none;
  appearance: none;
  outline: 0;
  background-color: transparent;
}
.header__label input:checked + .header__radio_fake {
  text-decoration: underline;
}
.header__label input:checked + .header__radio_fake::after {
  opacity: 1;
}

.header__label {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.header__radio_fake {
  position: relative;
  display: inline-block;
  width: 18px;
  height: 18px;
  margin-right: 12px;
  border-radius: 50%;
  border: 2px solid #002f6c;
}
.header__radio_fake::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background-color: #002f6c;
  opacity: 0;
  transition: opacity 0.2s ease-out;
}

.header__radio_text {
  font-weight: 500;
  font-size: 18px;
  color: #002f6c;
  white-space: nowrap;
}

.main {
  flex-grow: 1;
}

.intro {
